.component-container{
    width:fit-content;
}

.chips-container{
    padding-left:0px;
    width:300px;
    overflow:auto;
    white-space:nowrap;
}

div.the-chip{
    background-color:white;
    color:black;
    border:solid 0.5px;
}

span.chip-value {
    font-size: 10px;
    font-weight: bold;
    color: black;
    border: solid 1px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}